@import 'varibles'
@import 'mixin'

.habilidades
    width: 100vw
    height: auto
    min-height: 100vh
    position: relative
    
        
.txt-habilidades
    width: 40%
    height: 30%
    margin-left: 60px
    position: relative
    top: 80px

.titulos
    display: flex
    margin-bottom: 3px
    justify-content: center
    font-family: $fontItim

.titulos-alto
    font-size: 2.3em
    

.titulo-habilidades
    color: #003b62
    

.soft-skills
    display: flex
    flex-direction: column
    align-items: flex-start
    font-family: $fontItim
   
    & p
        font-size: 1.3em
        
    & p i 
        margin-right: 20px       
    
    & h3
        font-size: 1.5em
        margin-bottom: 10px

.ta 
    border-bottom: 2px solid var(--cor-texto)
    display: inline-block
    

.soft-skills p,h2,h1
    margin-top: 0
    margin-bottom: 0

.sobre-habilidades
    font-family: $fontItim
    font-size: 1.4em

.fa-angles-right
    color: #00195e

.listas-container
    display: flex 
    flex-wrap: wrap
    flex-direction: row 
    gap: 10px 
    width: 100%

.ul-habilidades-um ,.ul-habilidades-dois
    display: flex
    flex-direction: column
    flex: 1
    list-style: none
    margin-left: -40px

.icones-skills
    position: absolute
    right: 0
    top: 12%
    margin-right: 8%
    display: flex 
    flex-wrap: wrap 
    justify-content: space-between 
    gap: 20px 
    width: 40% 
    margin-top: 20px 

.icones-info
    flex: 1 1 calc(33.33% - 20px) 
    display: flex
    flex-direction: column 
    align-items: center 
    text-align: center 
    margin-bottom: 40px
    font-size: 1.2em
    font-family: $fontItim

.icones-info i
    transition: 0.3s ease-in-out
    

    &.fa-html5
        @include hover-skills(#e34c26) 

    &.fa-css3-alt
        @include hover-skills(#264de4) 

    &.fa-js
        @include hover-skills(#f7df1e) 
    
    &.fa-react
        @include hover-skills(#61dafb) 

    &.fa-node-js
        @include hover-skills(#68a063) 

    &.fa-sass
        @include hover-skills(#cf649a)

    &.fa-bootstrap
        @include hover-skills(#7952B3)
    





