@import 'varibles'
@import 'mixin'

.projetos
    width: 100vw
    height: 100vh
    min-height: 100vh
    position: relative 
    display: flex
    flex-direction: row 
    justify-content: space-around
   

.titulo-projetos
    display: flex
    flex-direction: column
    top: 8%
    position: absolute

    &::after
        content: ''
        background-color: #fff
        width: 70vw    
        height: 3px
        bottom: 0
        
.titulo-projetos-alto
    color: #00c489

.container-projetos
    background-color: var(--escuro-projetos)
    width: 25%
    height: 70%
    top: 25%
    position: relative
    display: flex
    flex-direction: column
    align-items: center
    border-radius: 25px
    

.fundo-projetos
    width: 90%
    height: 40%
    position: relative
    background-color: #fff
    border-radius: 25px
    margin-top: 15px
   

.abrigo-projetos
    display: flex 
    justify-content: center 
    align-items: center 
    position: relative
    width: 100% 
    height: 100% 

.abrigo-projetos img
    max-width: 100%
    max-height: 100% 
    object-fit: contain 
    position: relative

.text-projetos 
    margin-right: 10px
    margin-top: 15px
    width: 90%
    height: auto
    
    font-family: $fontItim
    
    & h2
        margin: 0
        border-bottom: 2px solid #fff
        font-size: 1.4em

.btn-link-site
    position: absolute
    bottom: 0
    left: 15px
    margin-bottom: 10px
    margin-top: 10px
    z-index: 10
    

    & button
        width: 100%
        height: 40px
        background-color: #055a6f
        border: none
        border-radius: 10px
        text-decoration: none
        color: var(--cor-texto)
        transition: transform 0.3s ease-in-out
        text-align: center
        font-family: $fontItim
        font-size: 1.2em

.btn-link-site button:hover
    transform: scale(1.2)
    transition: transform 0.3s ease-in-out 
    cursor: pointer

.skills-projeto
    position: absolute
    bottom: 0
    margin-bottom: 15px
    right: 10px

.skills-projeto i
    position: relative
   
    cursor: pointer

    &.fa-html5
        @include hover-skills(#e34c26) 

    &.fa-css3-alt
        @include hover-skills(#264de4) 

    &.fa-js
        @include hover-skills(#f7df1e) 
    
    &.fa-react
        @include hover-skills(#61dafb) 

    &.fa-node-js
        @include hover-skills(#68a063) 

    &.fa-sass
        @include hover-skills(#cf649a)

    &.fa-bootstrap
        @include hover-skills(#7952B3)
    
    
        



    

    