@import 'varibles'


.modal-contatos-container
    display: none 
    position: fixed
    top: 0
    left: 0
    width: 100vw
    height: 100vh
    background-color: var(--escuro-modal)
    justify-content: center
    align-items: center
    z-index: 1000

.modal
    background-color: #0d2a00
    padding: 20px
    border-radius: 20px
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2)
    text-align: center
    width: 50%
    height: 50%

.fechar-modal
    position: relative
    display: flex
    top: 10px
    background: none
    border: none
    font-size: 1.5em
    cursor: pointer

.modal-ativo
    display: flex



.tituulo-form-contatos
    font-family: $fontItim
    font-size: 1.9em
    text-align: center
    position: relative
    color: #fff

    &::after
        content: ''
        position: absolute
        background-color: #fff
        width: 40vw
        height: 3px
        bottom: -10px 
        left: 50%   
        transform: translateX(-50%) 


.container-dados-ctt
    display: flex
    align-items: center
    justify-content: center
    flex-direction: row
    gap: 10px
    width: 90%
    height: 100%
    margin-top: -5%

.dados-inicias-ctt
    display: flex
    flex-direction: column
    

.remetente, .contato-assunto
    height: 30px
    margin-bottom: 20px
    border: none
    border-radius: 25px
    text-align: center

.contato-corpo
    display: flex
    width: 50%
    height: 50%
    border-radius: 25px
    text-align: center

.enviar-ctt
    border-radius: 25px
    height: 30px
    border: none    
    background-color: #0e6f28
    position: relative

    &:hover
        background-color: #00af2f

.form-contato
    position: relative // Necess√°rio para que os elementos absolutos sejam posicionados em rela√ß√£o a este container
    display: flex
    flex-direction: column
    justify-content: flex-start
    align-items: center
    width: 100%
    height: 100%

.icones-ctt
    position: absolute 
    bottom: 10px 
    left: 50% 
    transform: translateX(-50%) 
    display: flex
    flex-direction: row
    justify-content: space-between
    align-items: center
    width: 20% 

.icones-ctt a
    color: #fff
    font-size: 1.6em

.icones-ctt a i
    &.fa-linkedin
        @include hover-skills(#0077B5)

    &.fa-envelope
        @include hover-skills(#D44638)

    &.fa-whatsapp
        @include hover-skills(#25D366)

    &.fa-github
        @include hover-skills(#181717)
