@import 'varibles'
    
.container-abouSkills
    width: 100vw
    height: 100vh 
    position: relative
    display: flex
    flex-direction: row
    justify-content: space-around
     
    & img
        position: relative
        top: 5%
        transition: 1.5s ease
        transform: scale(0.7)



.container-sobre
    width: 40vw
    height: 50%
    min-height: 40%
    font-size: 1.5em
    position: relative  
    top: 25%
    font-family: $fontItim
    line-height: 1.8
    border-radius: 25px
    text-align: left
    margin-left: 20px


.titulo-sobremim
    color: #efab00
    
        
   
    