.darkmode
    position: relative
    display: flex
    align-items: center
    
    
.fa-solid
    margin-right: -10px
    font-size: 1.3em
    transition: transform 0.4s ease 



.btn-container
    width: 60px
    height: 25px
    background-color: #ffffff79
    border-radius: 20px
    transition: opacity 0.6s ease, transform 0.6s ease
    cursor: pointer

    & button 
        position: relative
        top: 3px
        left: 5px
        width: 20px
        height: 20px
        border-radius: 50%
        transition: opacity 0.6s ease, transform 0.6s ease
        cursor: pointer
        
            
.modo-escuro .about-skills
    background-color: #4B3621
    position: relative

.modo-claro .about-skills
    background-color: #eab784
    

.modo-escuro .backgroud-habilidades
    background-color: #0f0227

.modo-claro .backgroud-habilidades
    background-color: #7f65ad

.modo-escuro .backgroud-projetos
    background-color: #410345

.modo-claro .backgroud-projetos
    background-color: #4c5c79

.esquerda
    background-color: #29272a
    position: absolute
    width: 40%
    height: 100vh
    z-index: -999999

.direita
    background-color: #192919
    position: absolute
    width: 60%
    height: 100vh
    right: 0
    z-index: -999999
    
.esquerda-claro
    background-color: #e1e1da
    position: absolute
    width: 40%
    height: 100vh
    z-index: -999999

.direita-claro
    background-color: #95b8a4
    position: absolute
    width: 60%
    height: 100vh
    right: 0
    z-index: -999999





