@import 'varibles'
@import 'animations'
@import 'darkmode'
@import 'pc_e_lampada'
@import 'stilosobre'
@import 'stilohabilidades'
@import 'stiloprojetos'
@import 'mixin'
@import 'modal_contatos'

html 
    scroll-behavior: smooth
    height: 100vh
   

body
    margin: 0
    padding: 0
    color: var(--cor-texto) 
    overflow-x: hidden
    

.home
    position: relative
    width: 100vw
    height: 100vh


.container-navbar
    position: fixed
    background-color: transparent
    right: 0    
    width: 100%
    height: 50px
    display: flex
    justify-content: flex-end
    align-items: center
    z-index: 99999999999    
    transition: background-color 0.3s ease-in-out 

    &.navbar-scroll
        background-color: var(--backgroud-navbar) // Cor de fundo quando o scroll est√° abaixo do topo   

    & .navbar   
        display: flex
        gap: 30px
        margin-right: 40px

.logo
    display: inline-flex
    flex-direction: row
    align-items: center
    position: absolute
    left: 20px
    font-family: $fontItim


    & a 
        display: inline-flex
        align-items: center
        text-decoration: none
        color: var(--cor-texto)
    & h1 
        color: #089e7b
        font-size: 2em
   
a.link-navbar
    position: relative 
    text-decoration: none
    font-family: $fontItim
    font-size: 1.3em
    color: inherit
    transition: 0.3s ease-in-out

    &:hover
        transform: scale(1.3)
        

    &::after
        content: ""
        position: absolute
        bottom: 0
        left: 0
        height: 3px
        width: 0 
        background-color: #005431
        border-radius: 25px
        transition: width 0.3s ease-in-out

.link-navbar.borda-ativa::after
    animation: borda-navbar 0.5s forwards

.link-navbar.retirar-borda::after
    animation: retirar-borda-navbar 0.5s forwards

.container-apresentacao
    position: relative
    right: -55%
    top: 5%
    max-width: 30%
    white-space: nowrap

    z-index: 1

    & h2 
        font-size: 2.5em
        font-family: $fontItim  
      
    & h1
        position: relative
        top: 10%
        right: 50px
        display: block
        font-size: 3.5em
        font-family: $fontItim  
        border-bottom: 2px solid #fff
        

.alternar-escrita
    position: relative
    font-size: 3.3vw
    color: #089e7b
    font-weight: bold
    top: -40px
    right: 50px
    animation: digitando 0.5s ease
    display: inline-block
    border-right: 2px solid #089e7b
    animation: piscar-cursor 0.7s steps(1) infinite
    font-size: 3em
    font-family: $fontItim  

      
    &::before
            content: "Desenvolvedor"
            font-weight: normal
            color: var(--texto-desenvolvedor)
            margin-right: 10px 
            display: inline-block


.click-container
    position: fixed
    width: 100%
    height: 100%
    pointer-events: none
    z-index: 9999999999999999999999
    top: 0
    left: 0

    .splash
        position: absolute
        animation: fadeOut 2s forwards
        opacity: 1
        transform: scale(1)

        & img
            width: 50px 
            height: 50px 
            pointer-events: none 
            color: #fff


.animar-lado
    opacity: 0
    transform: translateX(-100%)
    transition: all 0.5s ease-in-out

.animar-lado.ativo
    opacity: 1
    transform: translateX(0)

.animar-lado-esquerdo
    opacity: 0
    transform: translateX(100%)
    transition: all 0.5s ease-in-out

.animar-lado-esquerdo.ativo-esquerdo
    opacity: 1
    transform: translateX(0)


.redes-sociais-home
    display: flex
    flex-direction: column
    position: absolute // Posiciona em rela√ß√£o ao elemento pai (.home)
    bottom: 10px // Cola no fundo da se√ß√£o
    left: 10px // Ajusta a posi√ß√£o horizontal
    gap: 10px // Espa√ßamento entre os √≠cones

    & a
        font-size: 1.6em
        color: var(--cor-texto)
        transition: transform 0.3s ease-in-out // Suaviza o efeito de escala

        &:hover
            transform: scale(1.2) // Aumenta o tamanho no hover
    & a i 
        &.fa-linkedin
            @include hover-skills(#0077B5)

        &.fa-envelope
            @include hover-skills(#D44638)

        &.fa-whatsapp
            @include hover-skills(#25D366)

        &.fa-github
            @include hover-skills(#181717)
