@keyframes falhaLuz
    0% 
        background-color: #00000000 
        opacity: 0

    10%
        background-color: #6b6448 
        opacity: 0.8

    20%
        background-color: #6b6448
        opacity: 0.4

    30%
        background-color: #6b6448
        opacity: 1

    40%
        background-color: #00000000 
        opacity: 0

    50%
        background-color: #6b6448
        opacity: 0.6

    60%
        background-color: #6b6448
        opacity: 0.2

    70%
        background-color: #6b6448
        opacity: 1

    80%
        background-color: #6b6448
        opacity: 0.8

    90%
        background-color: #6b6448
        opacity: 0.5

    100%
        background-color: #6b6448 
        opacity: 1

@keyframes borda-navbar
    0%
        width: 0   
       
    100%
        width: 100%
        
@keyframes retirar-borda-navbar
    0%
        width: 100%
        
    100%
        width: 0
        

@keyframes fade-in-slide
    0%
        opacity: 0
        transform: translateX(100px)
    100%
        opacity: 1
        transform: translateX(0)

@keyframes bounce-in
    0%
        transform: scale(0.5)
        opacity: 0
    50%
        transform: scale(1.2)
        opacity: 0.8
    100%
        transform: scale(1)
        opacity: 1


@keyframes fadeOut 
    0% 
      opacity: 1
      transform: scale(1)
    
    100% 
      opacity: 0
      transform: scale(1.2)
    
@keyframes piscar-cursor
    0%, 100%
        border-color: transparent
    50%
        border-color: #089e7b