@import url('https://fonts.googleapis.com/css2?family=Noto+Sans:ital,wght@0,100..9001,100..900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Playfair+Display+SC:ital,wght@0,4000,7000,9001,4001,7001,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Itim&display=swap');

$fontItim: 'Itim'
$fontMaker: 'Playfair Display SC '
$fontNoto: 'Noto Sans'
$sizeFont: 1.6em
$borderAbaixoMac: 0px 0px 10px 10px

$cor-claro: #000
$cor-escuro: #fff

$fundo-escuro-projetos:#0f0d0d89
$fundo-claro-projetos:#5251517a

$backgroud-navbar-escuro: #050404ab
$backgroud-navbar-claro: #fdfdfdcc

$escuro-modal-ctt: #000000bd

:root
    --cor-texto: #{$cor-claro}
    --texto-desenvolvedor: #{$cor-claro}
    --escuro-projetos: #{$fundo-claro-projetos}
    --backgroud-navbar: #{$backgroud-navbar-claro}
    --escuro-modal: #{$escuro-modal-ctt}
    

body.modo-escuro
    --cor-texto: #{$cor-escuro}
    --texto-desenvolvedor: #{$cor-escuro}
    --escuro-projetos: #{$fundo-escuro-projetos}
    --backgroud-navbar: #{$backgroud-navbar-escuro}
    --escuro-modal: #{$escuro-modal-ctt}
    
   